import { all, call, fork, put, takeEvery } from 'redux-saga/effects';

import {
  LOGIN_USER,
  REGISTER_USER,
  VERIFY_OTP,
  LOGOUT_USER,
  FORGOT_PASSWORD,
  RESET_PASSWORD,
  PROFILE_ADD_DATA,
} from '../actions';

import {
  loginUserSuccess,
  loginUserError,
  registerUserSuccess,
  registerUserError,
  forgotPasswordSuccess,
  forgotPasswordError,
  resetPasswordSuccess,
  resetPasswordError,
  verifyOTPError,
  addProfileDataSuccess,
  addProfileDataError,
} from './actions';

import { adminRoot } from '../../constants/defaultValues';
import { setCurrentUser, setProfileData } from '../../helpers/Utils';
import instance from '../../helpers/axiosInstance';

export function* watchAddProfile() {
  // eslint-disable-next-line no-use-before-define
  yield takeEvery(PROFILE_ADD_DATA, addProfileData);
}

const addProfileDataAsync = async (fullName, userID, contactNo) => {
  console.log({
    full_name: fullName,
    user_id: userID,
    contact_no: contactNo,
  });

  const response = await instance({
    method: 'POST',
    url: '/addProfile',
    data: {
      full_name: fullName,
      user_id: userID,
      contact_no: contactNo,
    },
  });
  const { data } = response;
  return data;
};

function* addProfileData({ payload }) {
  const { fullName, userID, contactNo } = payload.profile;

  const { history } = payload;

  try {
    const profileData = yield call(
      addProfileDataAsync,
      fullName,
      userID,
      contactNo
    );

    console.log(profileData);
    if (profileData.success) {
      const { details } = profileData;
      setProfileData(details);
      yield put(addProfileDataSuccess(details));
      history.push(adminRoot);
    } else {
      yield put(addProfileDataError(profileData.message));
    }
  } catch (error) {
    yield put(addProfileDataError(error));
  }
}

export function* watchLoginUser() {
  // eslint-disable-next-line no-use-before-define
  yield takeEvery(LOGIN_USER, loginWithEmailPassword);
}

const loginWithEmailPasswordAsync = async (email, password) => {
  const response = await instance({
    method: 'POST',
    url: '/auth/login',
    data: { email, password },
  });

  const { data } = response;
  return data;
};

const getProfileDataAsync = async (id) => {
  const response = await instance({
    method: 'GET',
    url: `/profile/${id}`,
  });

  const { data } = response;

  if (data.success) {
    const { profile } = data;

    setProfileData(profile);
  } else {
    setProfileData();
  }
};

function* loginWithEmailPassword({ payload }) {
  const { email, password } = payload.user;
  const { history } = payload;
  try {
    const loginUser = yield call(loginWithEmailPasswordAsync, email, password);

    if (loginUser.success) {
      const { user } = loginUser.data;
      setCurrentUser(user);
      const { id } = user;
      yield put(loginUserSuccess(user));
      getProfileDataAsync(id);
      history.push(adminRoot);
    } else {
      yield put(loginUserError(loginUser.error));
    }
  } catch (error) {
    yield put(loginUserError(error));
  }
}

export function* watchRegisterUser() {
  // eslint-disable-next-line no-use-before-define
  yield takeEvery(REGISTER_USER, registerWithEmailPassword);
}

const registerWithEmailPasswordAsync = async (
  email,
  password,
  phone,
  userType = 0
) => {
  const response = await instance({
    method: 'POST',
    url: '/auth/register',
    data: { email, password, phone_number: phone, user_type: userType },
  });

  const { data } = response;

  return data;
};

function* registerWithEmailPassword({ payload }) {
  const { email, password, phone } = payload.user;
  const { history } = payload;

  try {
    const registerUser = yield call(
      registerWithEmailPasswordAsync,
      email,
      password,
      phone
    );

    if (registerUser.success) {
      window.localStorage.setItem('_token', registerUser.token);
      window.localStorage.setItem('_password', password);
      yield put(registerUserSuccess(registerUser.message));
      history.push('/user/verify-otp');
    } else {
      yield put(registerUserError(registerUser.message));
    }
  } catch (error) {
    yield put(registerUserError(error));
  }
}

export function* watchLogoutUser() {
  // eslint-disable-next-line no-use-before-define
  yield takeEvery(LOGOUT_USER, logout);
}

const logoutAsync = async (history) => {
  setCurrentUser(null);
  history.push(adminRoot);
};

function* logout({ payload }) {
  const { history } = payload;
  yield call(logoutAsync, history);
}

export function* watchForgotPassword() {
  // eslint-disable-next-line no-use-before-define
  yield takeEvery(FORGOT_PASSWORD, forgotPassword);
}

const forgotPasswordAsync = async (email) => {
  const response = await instance({
    method: 'POST',
    url: '/auth/forgotPassword',
    data: { email },
  });

  const { data } = response;
  return data;
};

function* forgotPassword({ payload }) {
  const { email } = payload.forgotUserMail;
  try {
    const forgotPasswordStatus = yield call(forgotPasswordAsync, email);
    if (!forgotPasswordStatus) {
      yield put(forgotPasswordSuccess('success'));
    } else {
      yield put(forgotPasswordError(forgotPasswordStatus.message));
    }
  } catch (error) {
    yield put(forgotPasswordError(error));
  }
}

export function* watchResetPassword() {
  // eslint-disable-next-line no-use-before-define
  yield takeEvery(RESET_PASSWORD, resetPassword);
}

const resetPasswordAsync = async (email, resetPasswordCode, newPassword) => {
  const response = await instance({
    method: 'POST',
    url: '/auth/forgotPassword',
    data: {
      email,
      password: newPassword,
      password_confirmation: newPassword,
      token: resetPasswordCode,
    },
  });

  const { data } = response;
  return data;
};

function* resetPassword({ payload }) {
  const { newPassword, resetPasswordCode } = payload;
  try {
    const resetPasswordStatus = yield call(
      resetPasswordAsync,
      resetPasswordCode,
      newPassword
    );
    if (!resetPasswordStatus) {
      yield put(resetPasswordSuccess('success'));
    } else {
      yield put(resetPasswordError(resetPasswordStatus.message));
    }
  } catch (error) {
    yield put(resetPasswordError(error));
  }
}

export function* watchVerifyOTP() {
  // eslint-disable-next-line no-use-before-define
  yield takeEvery(VERIFY_OTP, verifyOTP);
}

const verifyOTPAsync = async (OTP) => {
  const load = {
    otp: OTP,
    verify_token: window.localStorage.getItem('_token'),
    password: window.localStorage.getItem('_password'),
  };

  const response = await instance({
    method: 'POST',
    url: '/auth/verifyOtp',
    data: load,
  });

  const { data } = response;
  return data;
};

function* verifyOTP({ payload }) {
  const { OTP } = payload;
  const { history } = payload;

  try {
    const verifyOTPStatus = yield call(verifyOTPAsync, OTP);

    if (verifyOTPStatus.success) {
      const userData = verifyOTPStatus.data.user;
      window.localStorage.removeItem('_token');
      window.localStorage.removeItem('_password');
      setCurrentUser(userData);
      yield put(loginUserSuccess(userData));
      history.push('/user/complete-registration');
    } else {
      yield put(verifyOTPError(verifyOTPStatus.message));
    }
  } catch (error) {
    yield put(verifyOTPError(error));
  }
}

export default function* rootSaga() {
  yield all([
    fork(watchLoginUser),
    fork(watchLogoutUser),
    fork(watchRegisterUser),
    fork(watchVerifyOTP),
    fork(watchForgotPassword),
    fork(watchResetPassword),
    fork(watchAddProfile),
  ]);
}
